.btn {
  border: 0px;
  background: rgba(255, 255, 255, 0.8);
  color: rgba(0, 0, 0, 0.8);
  display: block;
  box-shadow: 1px 1px 4px rgba(0, 0, 0, 0.2), 0px 4px 10px rgba(0, 0, 0, 0.1);
  padding: 0.75em 2em;
  border-radius: 100px;
  font-family: inherit;
  display: block;
  text-decoration: none;
  width: max-content;
  transition: background 0.4s ease-in-out;
}

.btn:hover {
  background: var(--color-bg);
}
